.swap-card {
    display: flex;
    flex-flow: row wrap;
    background: $main;
    color: $white;
    & + & {
        margin-top: 40px; }
    &:nth-child(even) {
        flex-direction: row-reverse; }
    &__content {
        width: 50%;
        padding: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        @include r(768) {
            width: 100%;
            order: 1;
            padding: 40px 30px 45px 30px; }
        &--center {
            text-align: center; }
        &-inner {
            margin-top: -10px; } }
    &__title {
        font-family: $f-2;
        font-size: 40px;
        margin-bottom: 20px;
        @include r(768) {
            font-size: 25px;
            margin-bottom: 10px; } }
    &__text {
        font-family: $f-1;
        font-size: 18px;
        line-height: 1.22;
        @include r(768) {
            font-size: 15px; } }
    &__img {
        width: 50%;
        font-size: 0;
        position: relative;
        @include r(768) {
            width: 100%;
            order: 0; }
        &::before {
            content: '';
            display: block;
            width: 100%;
            padding-bottom: 63.44%; }
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover; } }
    .bttn {
        margin-top: 40px;
        min-width: 220px;
        @include r(768) {
            margin-top: 20px;
            min-width: 180px; } } }

.swap-card-wrap {
    margin-bottom: 50px;
    @include r(768) {
        margin-bottom: 30px; } }
